.header {
    display: flex;
    align-items: center;
    padding: 1rem;
    background-color: #f0f0f0;
  }
  
  .logo {
    width: 50px;
    height: 50px;
    margin-right: 1rem;
  }
  
  .progress {
    margin-left: auto;
    margin-right: 1rem;
  }
  
  .resetButton {
    padding: 0.5rem 1rem;
    background-color: #ff4040;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }