.wrapper {
  display: flex;
  flex-direction: column;
  flex: 1;
  padding: 1rem;
}

.playground {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
  
.column {
  flex: 1;
  min-width: 300px;
  margin-right: 1rem;
}

.explanation {
  /* background-color: #f0f0f0; */
