.queryEditor {
    display: flex;
    flex-direction: column;
  }
  
  .queryInput {
    padding: 1rem;
    font-family: monospace;
    font-size: 14px;
    border: 1px solid #ccc;
    border-radius: 4px;
    resize: none;
  }
  
  .executeButton {
    margin-top: 1rem;
    padding: 0.5rem 1rem;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .executeButton:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
  }