.sidebar {
    width: 250px;
    padding: 0 1rem;
    background-color: #f0f0f0;
    overflow-y: auto;
  }
  
  .chapter {
    margin-bottom: 1rem;
  }
  
  .list {
    padding: 0;
  }
  
  .task {
    cursor: pointer;
    padding: 0.5rem;
    margin-bottom: 0.25rem;
    border-radius: 4px;
    list-style: none;
  }

  .selected {
    cursor: pointer;
    padding: 0.5rem;
    margin-bottom: 0.25rem;
    border-radius: 4px;
    background-color: #4CAF50;
    color: white;
    list-style: none;
  }
  
  .task:hover {
    background-color: #e0e0e0;
  }
  
  .checkmark {
    color: green;
    margin-left: 0.5rem;
  }